import React, { useEffect, useState } from "react";
import "./Style.css";
import { LinkContainer } from "react-router-bootstrap";
import Nav from "react-bootstrap/Nav";
import axios from "axios";
import Card from "react-bootstrap/Card";

const Vocab = () => {
  const [gdata, setGdata] = useState();
  const getsectionData = async () => {
    const data = await axios.get("/vocab");
    console.log(data.data.gsetion);
    await setGdata(data.data.gsetion);
  };
  useEffect(() => {
    getsectionData();
  }, []);
  return (
    <>
      <div className="container border border-primary ">
        <div className="row  category-parent justify-content-evenly ">
          {gdata?.map((section) => (
            <div className=" col section1 sectionTab m-0  col-md-3 m-1 " >
              <h1>
                <LinkContainer to={`${section.topic}`} className="overlay ">
                  <Nav.Link>{section.topic}</Nav.Link>
                </LinkContainer>
              </h1>
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default Vocab;
