import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import Nav from "react-bootstrap/Nav";
import { LinkContainer } from "react-router-bootstrap";
import { useParams } from "react-router-dom";
import Gcontent from "./Gcontent";
import { useEffect } from "react";
import axios from "axios";
import ReactPlayer from "react-player/youtube";
import "./Style1.css";
import Fullplayer from "./Fullplayer";

function ESingleClass({ data, section, chapter, sss }) {
  console.log("14", data, section, chapter);

  const deleteClass = () => {
    console.log("delete class");
    const id = data._id;
    const { data1 } = axios.post(`/deleteclass`, {
      section,
      chapter,
      id,
    });
    sss();
  };

  return (
    <div className=" col-md-3 sectionTab m-0  ">
      <Card>
        {/* <h1>{data.title}</h1> */}
        {/* <Fullplayer data={data.link}/> */}

        {/* <ReactPlayer  className='.player' url={`https://www.youtube.com/watch?v=${data.link}`} controls /> */}

        <iframe
          src={`https://www.youtube.com/embed/&{data.link}`}
          title="YouTube video player"
        ></iframe>
        <Card.Body>
          <Card.Title>
            <h1>
              {" "}
              {data.title} {1}
            </h1>
          </Card.Title>
          <Card.Text>{data.desc}</Card.Text>
          {/* <Button variant="primary">
            <LinkContainer to={`&{data.link}`}>
              <Nav.Link>Edit</Nav.Link>
            </LinkContainer>
          </Button> */}
          <div className="d-block gap-2">
            <Button variant="danger   " className="btn " onClick={deleteClass}>
              delete
            </Button>
          </div>
        </Card.Body>
      </Card>
    </div>
  );
}

export default ESingleClass;
