import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import ShowEditClass from "./ShowEditClass";
import { useState, useEffect } from "react";
import axios from "axios";
import Singleclass from "../SingleClass";
import Card from "react-bootstrap/Card";
import ESingleClass from "../ESingleClass";
import { useParams } from "react-router-dom";

const AddClass = () => {
  const [section, setSection] = useState("grammar");
  const [chapter, setChapter] = useState("noun");
  const [chapter1, setChapter1] = useState();
  // const [gdata, setGdata] = useState();
  const [edit, setEdit] = useState();

  const [title, setTitle] = useState();
  const [desc, setDesc] = useState();
  const [link, setLink] = useState();


  

  const getsectionData = async () => {
    const data = await axios.get(`/${section}`);
    console.log(data.data.gsetion);
    await setChapter1(data.data.gsetion);
    await chapter;
  };

  const EditData = async () => {
    const { data } = await axios.get(
      `/edit/${section}/${chapter}`
    );
    await setEdit(data.edit[0].videos);
    await edit;
    console.log("32", edit);
    console.log("fr", section, chapter, data);
    console.log("27", data.edit[0].videos);
  };

  const aaa = chapter1?.map((topic) => topic.topic);
  console.log("22", chapter, aaa);
  useEffect(() => {
    getsectionData();
  }, [section]);

  const chaptersgrammar = aaa;

  const chaptersvocab = aaa;
  const chaptersphrases = aaa;
  const sections = aaa;

  let data;
  if (section === "grammar") {
    data = chaptersgrammar;
  } else if (section === "vocab") {
    data = chaptersvocab;
  } else if (section === "phrases") {
    data = chaptersphrases;
  }else if (section === "communication") {
    data = chaptersphrases;
  }
  console.log(section, chapter, title, desc, link);

  const postData = async () => {
    const { data } = await axios.post("/admin", {
      section,
      chapter,
      title,
      desc,
      link,
    });
    console.log("data post ");
  };

  const postDataSection = async () => {
    const { data } = await axios.post("/createsection", {
      section,
      chapter,
    });
    console.log(75, section, chapter);
    getsectionData();
  };
  console.log(chapter);
  return (
    <>
      <div className="container">
        <div className="row">
          {/* add section */}

          <Form className="bg-primary">
            <h1> Add section</h1>
            <select onChange={(e) => setSection(e.target.value)}>
              <option value="grammar">grammar</option>
              <option value="vocab">vocab</option>
              <option value="phrases">phrases</option>
              <option value="communication">communication</option>
            </select>

            <input
              placeholder="enter chapter"
              onChange={(e) => setChapter(e.target.value)}
            />

            <Button variant="danger" onClick={postDataSection}>
              Add class
            </Button>
          </Form>

          {/* add chapter */}

          <Form className="bg-warning">
            <h1> Add Chapter</h1>
            <select onChange={(e) => setSection(e.target.value)}>
              <option value="grammar">grammar</option>
              <option value="vocab">vocab</option>
              <option value="phrases">phrases</option>
              <option value="communication">communication</option>
            </select>

            <select onChange={(e) => setChapter(e.target.value)}>
              {data?.map((chapt) => (
                <option value={chapt}>{chapt}</option>
              ))}
            </select>

            <Form.Group className="mb-3">
              <Form.Label>title</Form.Label>
              <Form.Control
                placeholder=" Enter title"
                onChange={(e) => setTitle(e.target.value)}
              />
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label>desc</Form.Label>
              <Form.Control
                placeholder="Enter desc"
                onChange={(e) => setDesc(e.target.value)}
              />
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label>link</Form.Label>
              <Form.Control
                placeholder="Enter link"
                onChange={(e) => setLink(e.target.value)}
              />
            </Form.Group>

            <Button variant="primary" onClick={postData}>
              Add chpater
            </Button>
          </Form>
        </div>

        {/* edit chapter */}

        <div className="row">
          <Form className="bg-secondary">
            <h1> Edit Chapter</h1>
            <select onChange={(e) => setSection(e.target.value)}>
              <option value="grammar">grammar</option>
              <option value="vocab">vocab</option>
              <option value="phrases">phrases</option>
              <option value="communication">communication</option>
            </select>

            <select onChange={(e) => setChapter(e.target.value)}>
              {data?.map((chapt) => (
                <option value={chapt}>{chapt}</option>
              ))}
            </select>

            <Button variant="primary" onClick={EditData}>
              Edit class
            </Button>
          </Form>
        </div>
        <div className="row">
          {edit?.map((ecls) => (
            <ESingleClass
              data={ecls}
              section={section}
              sss={EditData}
              chapter={chapter}
            />
          ))}
        </div>
      </div>
      <p>all data is shown here </p>
    </>
  );
};

export default AddClass;
