import React, { useEffect, useState } from "react";
import "./Style.css";
import Singleclass from "./SingleClass";
import { useParams } from "react-router-dom";
import axios from "axios";

const Gcontent = () => {
  const [video, setVideo] = useState();
  console.log("9", video);
  const { gcontent, grammar } = useParams();
  console.log("gcontent", grammar, gcontent);
  const getGrammar = async () => {
    const { data } = await axios.get(
      `/${grammar}/${gcontent}`
    );
    await setVideo(data.data.videos);
   
  };

  useEffect(() => {
    getGrammar();
  }, []);

  console.log(video);
  return (
    <>
      <div className="container border border-primary ">
        <div className="row">
          {video?.map((clas) => (
            <Singleclass data={clas} />
          ))}
        </div>
      </div>
    </>
  );
};

export default Gcontent;
